.container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    background-color: rgb(10, 38, 64, 0.6);
    backdrop-filter: blur(8px);
    transition: all 0.5s ease-in-out;
}
